body, html {
  color: #000;
  font-family: sans-serif;
  font-weight: normal;
  font-size: 14px;
  margin: 0;
  padding: 0;
  background-color: #404447;
}

body {
  width: 726px;
  background-color: #fff;
  margin: 20px auto;
  box-shadow: 0 0 20px black;
  position: relative;
}

* {
  box-sizing: border-box;
}

.page-wrapper {
  display: block;
  width: 726px;
  height: 890px;
  page-break-inside: avoid;
  padding: 115px 45px 0;
}

.page-break {
  page-break-after: always;
  border-bottom: 1px dashed #888;
}

.row {
  float: none;
  width: auto;
  display: block;
  margin-left: -15px;
  margin-right: -15px;
}

.row:after {
  content: '';
  display: table;
  clear: both;
}

[class*="span-"] {
  display: block;
  float: left;
  padding-left: 15px;
  padding-right: 15px;
}

[class*="span-"]:last-child:not(:first-child) {
  float: right;
}

.span-1 {
  width: 33px;
}

.span-2 {
  width: 96px;
}

.span-3 {
  width: 159px;
}

.span-4 {
  width: 222px;
}

.span-5 {
  width: 285px;
}

.span-6 {
  width: 348px;
}

.span-7 {
  width: 411px;
}

.span-8 {
  width: 474px;
}

.span-9 {
  width: 537px;
}

.span-10 {
  width: 600px;
}

.span-11 {
  width: 663px;
}

.span-12 {
  width: 726px;
}

.span-last {
  float: right;
}

#header {
  position: absolute;
  padding: 45px 45px 10px;
  top: 0;
  left: 0;
  width: 100%;
  height: 100px;
}

#header .header-logo-1 {
  font-size: 30px;
  font-weight: 800;
  margin: 0;
  padding: 0;
  text-align: left;
  color: #006fff;
  display: block;
}

#header .header-logo-1 img {
  max-width: 100%;
  height: auto;
}

#header .header-logo-2 {
  font-size: 30px;
  font-weight: 800;
  margin: 0;
  padding: 0;
  text-align: right;
  color: #006fff;
  display: block;
}

#header .header-logo-2 img {
  max-width: 100%;
  height: auto;
}

h1 {
  font-weight: 800;
  font-size: 20px;
  margin: 0 0 10px;
}

h2 {
  font-weight: 800;
  font-size: 18px;
  margin: 0 0 10px;
}

h3 {
  font-weight: 800;
  font-size: 16px;
  margin: 0 0 10px;
}

h4 {
  font-weight: 800;
  font-size: 14px;
  margin: 0 0 10px;
}

h5 {
  font-weight: 500;
  font-size: 14px;
  margin: 0 0 10px;
}

h5 {
  font-weight: 800;
  font-size: 12px;
  margin: 0 0 10px;
}

p {
  margin: 0 0 10px;
}

hr {
  margin: 10px 0;
  border: 0 none;
  background: #ddd;
  height: 1px;
}

.blue {
  color: #1D68D4;
}

.green {
  color: #1DA553;
}

.orange {
  color: #EA5915;
}

.red {
  color: #DB0019;
}

.yellow {
  color: #f39c12;
}

.gray {
  color: #575757;
}

.gray-light {
  color: #7B7B7B;
}

.light {
  font-weight: 400;
}

.section {
  margin: 20px 0;
  page-break-inside: avoid;
}

.section-header {
  margin-bottom: 20px;
}

.graph {
  width: 100%;
}

.graph img {
  width: 100%;
  height: auto;
}

.graph.donut {
  position: relative;
  width: 100%;
  border-radius: 50%;
  overflow: hidden;
}

.graph.donut .inner {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  width: 82.5px;
  z-index: 2;
}

.graph.donut .inner .label {
  font-size: 12px;
  color: #888;
  line-height: 1;
  margin: 0 0 5px;
}

.graph.donut .inner .num {
  font-size: 30px;
  line-height: 1;
  margin: 0;
}

.legend {
  display: block;
  margin-bottom: 5px;
}

.legend .key {
  display: inline-block;
  width: 10px;
  height: 10px;
  margin-right: 10px;
  border-radius: 5px;
}

.legend .label {
  display: inline-block;
}

.border-right {
  border-right: 1px solid #ddd;
}

.border-left {
  border-left: 1px solid #ddd;
}

.border-top {
  border-top: 1px solid #ddd;
}

.border-bottom {
  border-bottom: 1px solid #ddd;
}

.text-left {
  text-align: left;
}

.text-right {
  text-align: right;
}

.text-center {
  text-align: center;
}

.uppercase {
  text-transform: uppercase;
}

.lowercase {
  text-transform: lowercase;
}

table {
  width: 100%;
}

table th, table td {
  padding: 5px;
  border-bottom: 1px solid #ddd;
}

table.tight th, table.tight td {
  padding: 2px;
}

table.small {
  font-size: 80%;
}

table tr:last-child td {
  border-bottom: 0 none;
}

.total-box h4 {
  text-transform: uppercase;
  margin-bottom: 5px;
}

.total-box p {
  font-size: 30px;
  margin-bottom: 0;
}
